    merge ({row, column, rowIndex, columnIndex}) {
      const props = ['agencyNo', 'bizSector']
      if (props.includes(column.property)) {
        if (row.rowSpan[column.property] > 1) {
          return [row.rowSpan[column.property], 1]
        } else if (row.rowSpan[column.property] === 0) {
          return [0, 0]
        }
      }
    },
    analysis () {
      this.$router.push('/admin/agency/config/analysis')
    },
    change (vals) {
      console.log(vals)
      this.data2 = []
      if (vals.length === 0) {
        this.t2HiddenFlag = true
      } else {
        this.t2HiddenFlag = false
        this.allData.forEach(item => {
          if (vals.includes(item.code)) {
            item.data.forEach(dataItem => {
              this.data2.push(dataItem)
            })
          }
        })
      }
    }
  },
  computed: {
    mergeData () {
      const props = ['agencyNo', 'bizSector']
      return tableDataMerge(this.data2, props)
    }
